*,
*::before,
*::after {
  box-sizing: border-box;
}

:root {
  --ff-primary: 'Barlow', sans-serif;
  --ff-secondary: 'Azeret Mono', monospace;

  --fw-reg: 300;
  --fw-bold: 900;

  --clr-light: #869397;
  --clr-dark: #041e42;
  --clr-accent: #003594;

  --fs-h1: 3rem;
  --fs-h2: 2.25rem;
  --fs-h3: 1.25rem;
  --fs-body: 1rem;

  --bs: 0.25em 0.25em 0.75em rgba(0, 0, 0, .25),
        0.125em 0.125em 0.25em rgba(0, 0, 0, .15);
}

@media (min-width: 800px) {
  :root {
    --fs-h1: 4.5rem;
    --fs-h2: 3.75rem;
    --fs-h3: 1.5rem;
    --fs-body: 1.125rem;
  }
}

html {
  scroll-behavior: smooth;
}

body {
  background: var(--clr-light);
  color: var(--clr-dark);
  margin: 0;
  font-family: var(--ff-primary);
  font-size: var(--fs-body);
  line-height: 1.6;
}

section {
  padding: 5em 2em;
}

img {
  display: block;
  max-width: 100%;
}

strong {
  font-weight: var(--fw-bold);
}

:focus {
  outline: 3px solid var(--clr-accent);
  outline-offset: 3px;
}

.btn {
  display: inline-block;
  padding: .5em 2.5em;
  background: var(--clr-accent);
  text-decoration: none;
  cursor: pointer;
  font-size: .8rem;
  text-transform: uppercase;
  letter-spacing: 2px;
  font-weight: var(--fw-bold);
  transition: transform 200ms ease-in-out;
  color: white;
}

.btn:hover {
  transform: scale(1.1);
}

h1,
h2,
h3 {
  line-height: 1;
  margin: 0;
}

h1 {
  font-size: var(--fs-h1);
}

h2 {
  font-size: var(--fs-h2);
}

h3 {
  font-size: var(--fs-h3);
}

.section__title {
  margin-bottom: .25em;
}

.section__title--intro {
  font-weight: var(--fw-reg);
}

.section__title--intro > strong {
  display: block;
}

.section__subtitle {
  margin: 0;
  font-size: var(--fs-h3);
}

.section__subtitle--intro,
.section__subtitle--about {
  background: var(--clr-accent);
  padding: .25em 1em;
  font-family: var(--ff-secondary);
  margin-bottom: 1em;
  color: white;
}

.section__subtitle--projects {
  color: white;
  font-weight: var(--fw-bold);
  margin-bottom: 2em;
}

header {
  display: flex;
  justify-content: space-between;
  padding: 1em;
}

.logo {
  max-width: 150px;
  font-size: var(--fs-h3);
  font-weight: var(--fw-bold);
  color: var(--clr-accent);
}

.nav {
  position: fixed;
  background: var(--clr-dark);
  color: var(--clr-light);
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 100;

  transform: translateX(100%);
  transition: transform 250ms cubic-bezier(.5, 0, .5, 1);
}

.nav__list {
  list-style: none;
  display: flex;
  height: 100%;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  margin: 0;
  padding: 0;
}

.nav__link {
  color: inherit;
  font-weight: var(--fw-bold);
  font-size: var(--fs-h2);
  text-decoration: none;
}

.nav__link:hover {
  color: var(--clr-accent);
}

.nav-toggle {
  padding: .5em;
  background: transparent;
  border: 0;
  cursor: pointer;
  position: absolute;
  right: 1em;
  top: 1em;
  z-index: 1000;
}

.nav-open .nav {
  transform: translateX(0);
}

.nav-open .nav-toggle {
  position: fixed;
}

.nav-open .hamburger {
  transform: rotate(.625turn);
}

.nav-open .hamburger::before {
  transform: rotate(90deg) translateX(-6px);
}

.nav-open .hamburger::after {
  opacity: 0;
}

.hamburger {
  display: block;
  position: relative;
}

.hamburger,
.hamburger::before,
.hamburger::after {
  background: var(--clr-accent);
  width: 2em;
  height: 3px;
  border-radius: 1em;
  transition: transform 250ms ease-in-out;
}

.hamburger::before,
.hamburger::after {
  content: '';
  position: absolute;
  left: 0;
  right: 0;
}

.hamburger::before {
  top: 6px;
}

.hamburger::after {
  bottom: 6px;
}

.intro {
  position: relative;
}

.intro__img {
  box-shadow: var(--bs);
}

@media (min-width: 600px) {
  .intro {
    display: grid;
    width: min-content;
    margin: 0 auto;
    grid-column-gap: 1em;
    grid-template-areas: 
      "img title"
      "img subtitle";
    grid-template-columns: min-content max-content;
  }

  .intro__img {
    grid-area: img;
    min-width: 250px;
    position: relative;
    z-index: 2;
  }

  .section__subtitle--intro {
    align-self: start;
    grid-column: -1 / 1;
    grid-row: 2;
    text-align: right;
    position: relative;
    left: -1.5em;
    width: calc(100% + 1.5em);
  }
}

.my-skills {
  background-color: var(--clr-dark);
  background-image: url(../public/images/services-bg.jpg);
  background-size: cover;
  background-blend-mode: multiply;
  color: var(--clr-light);
  text-align: center;
}

.section__title--skills {
  color: white;
  position: relative;
}

.section__title--skills::after {
  content: '';
  display: block;
  width: 2em;
  height: 1px;
  margin: 0.5em auto 1em;
  background: var(--clr-light);
  opacity: 0.25;
}

.skills {
  margin-bottom: 4em;
}

.skill {
  max-width: 500px;
  margin: 0 auto;
}

.skill-logo {
  width: 171.2px;
  height: 171.2px;
  margin-top: 15px;
}

@media (min-width: 320px) and (max-width: 780px) {
  .skill {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 25px;
  }
}

@media (min-width: 800px) {
  .skills {
    display: flex;
    max-width: 1000px;
    margin-left: auto;
    margin-right: auto;
    gap: 2em;
  }
}

.about-me {
  max-width: 1000px;
  margin: 0 auto;
}

.about-me__img {
  box-shadow: var(--bs);
}

@media (min-width: 600px) {
  .about-me {
    display: grid;
    grid-template-columns: 1fr 200px;
    grid-template-areas: 
      "title img"
      "subtitle img"
      "text img";
    grid-column-gap: 2em;
  }

  .section__title--about {
    grid-area: title;
  }

  .section__subtitle--about {
    grid-column: 1 / -1;
    grid-row: 2;
    position: relative;
    left: -1em;
    width: calc(100% + 2em);
    padding-left: 1em;
    padding-right: calc(200px + 2 4em);
  }

  .about-me__img {
    grid-area: img;
    position: relative;
    z-index: 2;
    height: 76%;
  }
}

.projects {
  background-color: var(--clr-dark);
  color: var(--clr-light);
  text-align: center;
  padding-bottom: 1em;
}

.port-list {
  display: grid;
  grid-template-columns: auto;
  grid-template-rows: auto;
  gap: 2.5em;
  padding: 2.5em;
  justify-items: center;
  padding-top: 0;
  background-color: var(--clr-dark);
  color: white;
}

@media (min-width: 1440px) {
  .port-list {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (min-width: 1920px) {
  .port-list {
    grid-auto-flow: column;
    grid-auto-columns: minmax(0, 1fr);
  }
}

.portfolio {
  max-width: min(80vw, 25em);
  display: grid;
  grid-template-areas: 
    'img'
    'content';
  transform: scale(1);
  transition: all 500ms cubic-bezier(0.57, 2.2, 0.26, 0.99);
}

.portfolio:hover {
  box-shadow: var(--bs);
  transform: scale(1.0125);
}

.port-pic {
  grid-area: img;
  border-radius: 10px 10px 0 0;
  display: block;
  width: 100%;
  height: 100%;
  overflow: hidden;
  line-height: 0;
}

.port-info {
  grid-area: content;
  padding: 1.5em;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background-color: var(--clr-accent);
  border-radius: 0 0 10px 10px;
}

.port-title {
  font-size: var(--fs-h3);
  font-weight: var(--fw-bold);
  margin: 0;
  line-height: 1;
}

.port-desc {
  display: block;
  margin-block-start: 1em;
  margin-block-end: 1em;
  margin-inline-start: 0px;
  margin-inline-end: 0px;
}

.port-btns {
  display: flex;
  align-items: initial;
  gap: 0.5em;
}

.port-btns > a {
  padding: 0.5rem 0.75rem 0.5rem 1rem;
  text-align: center;
  text-decoration: none;
  color: var(--clr-dark);
  background-color: var(--clr-light);
  font-weight: var(--fw-bold);
  border-radius: 10px;
  transition: transform 200ms ease-in-out;
}

.port-btns > a:hover {
  transform: scale(1.1);
}

.footer {
  background: #111;
  color: white;
  text-align: center;
  padding: 2.5em 0;
  font-size: var(--fs-h3);
}

.footer > a {
  text-decoration: none;
}

.fa-icon {
  color: aliceblue;
}

.footer__text {
  font-weight: var(--fw-bold);
}

.social-list__link:hover {
  opacity: .7;
}

.social-list {
  list-style: none;
  display: flex;
  justify-content: center;
  margin: 2em 0 0;
  padding: 0;
}

.social-list__item {
  margin: 0 .5em;
}

.social-list__link {
  padding: .5em;
}